import React, { useState, useEffect, useRef } from 'react';
import { firestore, collection, auth, where, getDocs } from "../firebase"; 
import ExpertBox from './ExpertBox'
import Fuse from 'fuse.js';

const ChatExperts = ({ isOpen, toggleOpen, selectedTopic, firstUserMessage, selectedSession }) => {
  const [isDropdownOpen, setIsDropdownOpen] = useState(false);
  const [isIconRotated, setIsIconRotated] = useState(false);
  const [matchingExperts, setMatchingExperts] = useState([]);
  const chatExpertsRef = useRef(null);
  const [isHovered, setIsHovered] = useState(false);

  const [showExpertBox, setShowExpertBox] = useState(false);
  const [selectedExpert, setSelectedExpert] = useState(null);

  const [buttonClicked, setButtonClicked] = useState(false);
  


  const handleExpertBoxClose = () => {
    setShowExpertBox(false);
    setButtonClicked(false); // Reset buttonClicked state
    setIsHovered(null);
  };

  const handleMouseEnter = (index) => {
    setIsHovered(index);
  };

  const handleButtonClick = (expertInfo, index) => {
    if (index === isHovered) {
      setSelectedExpert(expertInfo);
      setButtonClicked(!buttonClicked); // Toggle buttonClicked state
      setShowExpertBox(true); // Always show ExpertBox on button click
    }
  };


  const handleMouseLeave = () => {
    if (!buttonClicked && !showExpertBox) {
      setShowExpertBox(false);
      setIsHovered(null);
    }
  };

  
  const toggleDropdown = () => {
    setIsDropdownOpen(!isDropdownOpen);
    setIsIconRotated(!isIconRotated);
    toggleOpen(); // Toggle ChatExperts
  };

  const handleClickOutside = (event) => {
    if (chatExpertsRef.current && !chatExpertsRef.current.contains(event.target)) {
      toggleOpen();
      setIsDropdownOpen(false);
      setIsIconRotated(false);
    }
  };

  useEffect(() => {
    const isMobile = window.innerWidth <= 768; // Set your mobile width threshold
    if (isOpen && isMobile) {
      document.addEventListener('mousedown', handleClickOutside);
    } else {
      document.removeEventListener('mousedown', handleClickOutside);
    }
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [isOpen]);

  useEffect(() => {
    const fetchMatchingExperts = async () => {
      if (isOpen) {
        try {
          let experts;
  
          if (selectedTopic) {
            console.log("Fetching experts for selected topic:", selectedTopic);
            experts = await getMatchingExpertsForTopic(selectedTopic, selectedSession);
          } else if (selectedSession) {
            console.log("Fetching experts for selectedSession:", selectedSession);
            experts = await getMatchingExperts(selectedSession);
          }
  
          console.log("Matching experts:", experts);
  
          setMatchingExperts(experts || []);
        } catch (error) {
          console.error("Error fetching experts:", error);
        }
      }
    };
  
    fetchMatchingExperts();
  }, [isOpen, selectedTopic, selectedSession]);
  
  

  const getMatchingExperts = async (selectedSession) => {
    try {
        const expertsCollection = collection(firestore, "expertApplications");
        const querySnapshot = await getDocs(expertsCollection);

        // Log the number of documents in expertApplications collection
        console.log("Number of documents in expertApplications:", querySnapshot.size);

        const uid = auth.currentUser.uid;
        const messagesCollection = collection(firestore, `users/${uid}/discussions/${selectedSession}/messages`);
        const messagesSnapshot = await getDocs(messagesCollection);

        // Log the number of documents in messages collection for the selected session
        console.log("Number of documents in messages collection for selected session:", messagesSnapshot.size);

        const matchingExperts = [];
        const processedExperts = new Set(); // Set to store processed expert UIDs

        messagesSnapshot.forEach((messageDoc) => {
            const messageData = messageDoc.data();
            const { prompt, response } = messageData;

            console.log("Message Data - Prompt:", prompt);
            console.log("Message Data - Response:", response);

            querySnapshot.forEach((doc) => {
                const data = doc.data();
                if (data && data.displayName && !processedExperts.has(doc.id)) {
                    const expertInfo = {
                        name: data.displayName,
                        photoURL: data.photoURL || `${process.env.PUBLIC_URL}/images/arts/user.webp`,
                        skills: data.keywords || [],
                        description: data.description,
                        city: data.city,
                        occupation: data.occupation,
                        otherUserUID: doc.id,
                    };

                    const fieldsToCheck = ['city', 'occupation', 'description', 'labels'];

                    const fuseOptions = {
                        keys: fieldsToCheck,
                        includeScore: true,
                        includeMatches: true,
                        threshold: 0.7,
                    };

                    const fuse = new Fuse([data], fuseOptions);

                    const sessionMatch = selectedSession && data.occupation.toLowerCase().includes(selectedSession.toLowerCase());

                    const promptMatch = prompt && fuse.search(prompt).length > 0;
                    const responseMatch = response && fuse.search(response).length > 0;

                    console.log("Expert Info:", expertInfo);
                    console.log("Fields in Firestore:", fieldsToCheck.map(field => `${field}: ${data[field].toLowerCase()}`)); // Use toLowerCase()
                    console.log("Session Match:", sessionMatch);
                    console.log("Prompt Match:", promptMatch);
                    console.log("Response Match:", responseMatch);

                    // Return expert if at least one condition is true
                    if (sessionMatch || promptMatch || responseMatch) {
                        matchingExperts.push(expertInfo);
                        processedExperts.add(doc.id); // Add expert UID to processed set
                    }
                }
            });
        });

        console.log("Matching experts for selected session:", matchingExperts);
        console.log("Selected Session:", selectedSession);

        return matchingExperts;
    } catch (error) {
        console.error("Error fetching experts for selected session:", error);
        return [];
    }
};


  const getMatchingExpertsForTopic = async (selectedTopic, firstUserMessage) => {
    try {
      const expertsCollection = collection(firestore, "expertApplications");
      const querySnapshot = await getDocs(expertsCollection);
      const matchingExperts = [];
  
      querySnapshot.forEach((doc) => {
        const data = doc.data();
        if (data && data.displayName) {
          const expertInfo = {
            name: data.displayName,
            photoURL: data.photoURL || `${process.env.PUBLIC_URL}/images/arts/user.webp`,
            skills: data.keywords || [],
            description: data.description,
            city: data.city,
            occupation: data.occupation,
            otherUserUID: doc.id,
          };
  
          // Check if there is a match in any of the specified fields or if there is no selected topic
          const fieldsToCheck = ['city', 'occupation', 'keywords', 'description', 'labels'];
  
          if (
            (firstUserMessage && fieldsToCheck.some(field => 
              data[field] &&
              typeof data[field] === 'string' &&
              data[field].toLowerCase().includes(firstUserMessage.toLowerCase())
            )) ||
            (selectedTopic && fieldsToCheck.some(field => 
              data[field] &&
              typeof data[field] === 'string' &&
              data[field].toLowerCase().includes(selectedTopic.name.toLowerCase())
            )) ||
            (!selectedTopic && !firstUserMessage) // Include expert if no selected topic or firstUserMessage
          ) {
            matchingExperts.push(expertInfo);
          }
        }
      });
  
      console.log("Matching experts for selected topic:", matchingExperts);
      console.log("Selected Topic:", selectedTopic);
      console.log("First User Message:", firstUserMessage);
  
      return matchingExperts;
    } catch (error) {
      console.error("Error fetching experts for selected topic:", error);
      return [];
    }
  };
  
  
  
  
  return (
    <div ref={chatExpertsRef} className={`chatbot-expert ${isOpen ? 'chatbot-expert-open' : ''}`}>
       
      <span className={`svg-chatbot-expert ${isOpen ? 'svg-chatbot-expert-rotate-icon' : ''}`} aria-hidden="true" onClick={toggleDropdown}>
        <svg xmlns="http://www.w3.org/2000/svg" color='#212529' fill="none" viewBox="0 0 24 24" strokeWidth={1} stroke="currentColor" height="25" width="25" style={{ marginLeft: 'auto' }}>
          <path strokeLinecap="round" strokeLinejoin="round" d="M12 18v-5.25m0 0a6.01 6.01 0 001.5-.189m-1.5.189a6.01 6.01 0 01-1.5-.189m3.75 7.478a12.06 12.06 0 01-4.5 0m3.75 2.383a14.406 14.406 0 01-3 0M14.25 18v-.192c0-.983.658-1.823 1.508-2.316a7.5 7.5 0 10-7.517 0c.85.493 1.509 1.333 1.509 2.316V18" />
        </svg>
      </span>
      {isOpen && (
        <>
      <div>
        <h4>Experts</h4>
      </div>
      {matchingExperts.map((expertInfo, index) => (
        <div key={index}>
        <button
          className={`chat-btn expert-btn text-left ${showExpertBox && index === isHovered ? 'open-expert-box' : ''}`}
          onMouseEnter={() => handleMouseEnter(index)}
          onMouseLeave={handleMouseLeave}
          onClick={() => handleButtonClick(expertInfo, index)}
        >
      <div className='d-flex flex-column'>
        <div className='d-flex align-items-center justify-content-between'>
          <img
            src={expertInfo.photoURL}
            alt="Avatar"
            className="very-small-avatar rounded-circle"
          />
          <div className="ml-2">
            <p className="font-weight-bold mb-0 text-start">{expertInfo.name}</p>
          </div>
        </div>
        {expertInfo.skills && (
          <div className="text-start mt-2">
            <p className="font-weight-bold mb-0">Skills: {expertInfo.skills.join(', ')}</p>
          </div>
        )}
      </div>
      {/* Render the ExpertBox component with isHovered prop */}
 
          </button>
          <ExpertBox
              isHovered={index === isHovered}
              displayName={expertInfo.name}
              photoURL={expertInfo.photoURL}
              skills={expertInfo.skills}
              description={expertInfo.description}
              city={expertInfo.city}
              occupation={expertInfo.occupation}
              otherUserUID={expertInfo.otherUserUID}
              selectedSession={selectedSession} 
              onClose={handleExpertBoxClose} // Pass the onClose callback
            
            />
        </div>
      ))}
     </>
      )}
    </div>
  );
};

export default ChatExperts;