import React, { useState, useEffect, useRef } from 'react';
import { auth, firestore } from '../firebase';
import { collection, query, orderBy, getDocs } from 'firebase/firestore';
import ShareChat from './sharechat';

const ChatHistory = ({ isOpen, toggleOpen, onSessionClick, resetChatMessages, selectedSession }) => {
  const [isDropdownOpen, setIsDropdownOpen] = useState(false);
  const [isIconRotated, setIsIconRotated] = useState(false);
  const chatHistoryRef = useRef(null);
  const [sessions, setSessions] = useState([]);
  const [isShareChatOpen, setIsShareChatOpen] = useState(false);
  const [isLoadingSessions, setIsLoadingSessions] = useState(true);

  const groupSessionsByDate = (sessions) => {
    const today = new Date().setHours(0, 0, 0, 0);
    const yesterday = new Date();
    yesterday.setDate(yesterday.getDate() - 1);
    yesterday.setHours(0, 0, 0, 0);

    const groupedSessions = {
      today: [],
      yesterday: [],
      previously: [],
    };

    sessions.forEach((session) => {
      const sessionDate = new Date(session.timestamp.seconds * 1000).setHours(0, 0, 0, 0);

      if (sessionDate === today) {
        groupedSessions.today.push(session);
      } else if (sessionDate === yesterday) {
        groupedSessions.yesterday.push(session);
      } else {
        groupedSessions.previously.push(session);
      }
    });

    return groupedSessions;
  };

  const [groupedSessions, setGroupedSessions] = useState({
    today: [],
    yesterday: [],
    previously: [],
  });

  useEffect(() => {
    if (isOpen) {
      // Fetch sessions from Firestore when the dropdown is open
      fetchSessions();
    }
  }, [isOpen]);



  const fetchSessions = async () => {
    try {
      const user = auth.currentUser;
      if (user) {
        const userUid = user.uid;

        const q = query(
          collection(firestore, 'users', userUid, 'discussions'),
          orderBy('timestamp', 'desc')
        );

        const snapshot = await getDocs(q);
        const sessionData = snapshot.docs.map((doc) => ({
          id: doc.id,
          timestamp: doc.data().timestamp,
        }));
        setSessions(sessionData);

        setGroupedSessions(groupSessionsByDate(sessionData));

        console.log('Fetched sessions:', sessionData);

        if (sessionData.length > 0) {
          const newlyCreatedDiscussion = sessionData[0].id;
        }

        setIsLoadingSessions(false);
      } else {
        console.error('User not authenticated');
        setIsLoadingSessions(false);
      }
    } catch (error) {
      console.error('Error fetching sessions from Firestore:', error);
      setIsLoadingSessions(false);
    }
  };



  const startNewChat = () => {
    resetChatMessages();
    console.log('Discussion set to undefined');
    onSessionClick(null);
  };

  useEffect(() => {
    if (isOpen) {
      fetchSessions();
    }
  }, [isOpen]);

  const toggleDropdown = () => {
    toggleOpen();
    setIsDropdownOpen(!isDropdownOpen); // Toggle the button visibility when dropdown is opened/closed
  };

  const handleClickOutside = (event) => {
    if (chatHistoryRef.current && !chatHistoryRef.current.contains(event.target)) {
      toggleOpen();
      setIsDropdownOpen(false);
      setIsIconRotated(false);
    }
  };

  useEffect(() => {
    const isMobile = window.innerWidth <= 768;
    if (isOpen && isMobile) {
      document.addEventListener('mousedown', handleClickOutside);
    } else {
      document.removeEventListener('mousedown', handleClickOutside);
    }
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [isOpen]);



  const closeShareChat = () => {
    setIsShareChatOpen(false);
  };
  
  useEffect(() => {
    if (isOpen) {
      // Fetch sessions from Firestore when the dropdown is open
      fetchSessions();
    }
  }, [isOpen]);

  return (
    <div ref={chatHistoryRef} className={`chatbot-history ${isOpen ? 'chatbot-history-open' : ''}`}>
     
      <span className={`svg-chatbot-history ${isOpen ? 'svg-chatbot-history-rotate-icon' : ''}`} aria-hidden="true" onClick={toggleDropdown}>
        <svg width="8" height="16" viewBox="0 0 8 16">
          <path d="M0.772126 1.19065L0.153407 1.80934C0.00696973 1.95578 0.00696973 2.19322 0.153407 2.33969L5.80025 8L0.153407 13.6603C0.00696973 13.8067 0.00696973 14.0442 0.153407 14.1907L0.772126 14.8094C0.918563 14.9558 1.156 14.9558 1.30247 14.8094L7.84666 8.26519C7.99309 8.11875 7.99309 7.88131 7.84666 7.73484L1.30247 1.19065C1.156 1.04419 0.918563 1.04419 0.772126 1.19065Z"></path>
        </svg>
      </span>
      {isOpen && (
        <>
   
        <div className='chatbot-previous'>
          <div style={{ display: 'flex' }}>
            <button
              className="chat-history-btn"
              onClick={() => {
                startNewChat();
                if (window.innerWidth <= 768) {
                  toggleDropdown();
                }
              }}
            >
              <div style={{ display: 'flex', alignItems: 'center' }}>
                <img src={`${process.env.PUBLIC_URL}/images/arts/TIO.webp`} alt="TIO illustration" className="me-1 very-small-avatar rounded-circle" />
                <p className="font-weight-bold mb-0" style={{ color: 'white', marginLeft: '5px' }}>Let's Chat</p>
              </div>
              <div className='ms-auto'>
                <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="white" width="18" height="18">
                  <path strokeLinecap="round" strokeLinejoin="round" d="m16.862 4.487 1.687-1.688a1.875 1.875 0 1 1 2.652 2.652L10.582 16.07a4.5 4.5 0 0 1-1.897 1.13L6 18l.8-2.685a4.5 4.5 0 0 1 1.13-1.897l8.932-8.931Zm0 0L19.5 7.125M18 14v4.75A2.25 2.25 0 0 1 15.75 21H5.25A2.25 2.25 0 0 1 3 18.75V8.25A2.25 2.25 0 0 1 5.25 6H10" />
                </svg>
              </div>
            </button>
          </div>
          <p className="text-sm text-muted mt-3">Today:</p>
          {groupedSessions.today.length > 0 && (
            <ul className="fade-ulist no-scroll">
            {groupedSessions.today.map((session) => (
              <li
                className={`fade-list${session.id === selectedSession ? ' selected-session' : ''}`}
                key={session.id}
                onClick={() => {
                  onSessionClick(session.id);
                  if (window.innerWidth <= 768) {
                    toggleDropdown();
                  }
                }}
              >
                {session.id}
                <div className="svg-chathistory-container">
<svg
xmlns="http://www.w3.org/2000/svg"
width="24"
height="24"
viewBox="0 0 24 24"
fill="none"
stroke="white"
strokeWidth="2"
strokeLinecap="round"
strokeLinejoin="round"
onClick={() => {
setIsShareChatOpen(true);
}}
>
<circle cx="12" cy="12" r="1"></circle>
<circle cx="19" cy="12" r="1"></circle>
<circle cx="5" cy="12" r="1"></circle>
</svg>

<svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="white" width="18" height="18">
<path strokeLinecap="round" strokeLinejoin="round" d="M3.75 9.776c.112-.017.227-.026.344-.026h15.812c.117 0 .232.009.344.026m-16.5 0a2.25 2.25 0 0 0-1.883 2.542l.857 6a2.25 2.25 0 0 0 2.227 1.932H19.05a2.25 2.25 0 0 0 2.227-1.932l.857-6a2.25 2.25 0 0 0-1.883-2.542m-16.5 0V6A2.25 2.25 0 0 1 6 3.75h3.879a1.5 1.5 0 0 1 1.06.44l2.122 2.12a1.5 1.5 0 0 0 1.06.44H18A2.25 2.25 0 0 1 20.25 9v.776" />
</svg>
</div>

              </li>
              
            ))}
          </ul>
          )}

<p className="text-sm text-muted mt-3">{groupedSessions.yesterday.length > 0 && 'Yesterday:'}</p>
{groupedSessions.yesterday.length > 0 && (
  <ul className="fade-ulist no-scroll">
             {groupedSessions.today.map((session) => (
               <li
                 className={`fade-list${session.id === selectedSession ? ' selected-session' : ''}`}
                 key={session.id}
                 onClick={() => {
                   onSessionClick(session.id);
                   if (window.innerWidth <= 768) {
                     toggleDropdown();
                   }
                 }}
               >
                 {session.id}
                 <div className="svg-chathistory-container">
 <svg
 xmlns="http://www.w3.org/2000/svg"
 width="24"
 height="24"
 viewBox="0 0 24 24"
 fill="none"
 stroke="white"
 strokeWidth="2"
 strokeLinecap="round"
 strokeLinejoin="round"
 onClick={() => {
 setIsShareChatOpen(true);
 }}
 >
 <circle cx="12" cy="12" r="1"></circle>
 <circle cx="19" cy="12" r="1"></circle>
 <circle cx="5" cy="12" r="1"></circle>
 </svg>
 
 <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="white" width="18" height="18">
 <path strokeLinecap="round" strokeLinejoin="round" d="M3.75 9.776c.112-.017.227-.026.344-.026h15.812c.117 0 .232.009.344.026m-16.5 0a2.25 2.25 0 0 0-1.883 2.542l.857 6a2.25 2.25 0 0 0 2.227 1.932H19.05a2.25 2.25 0 0 0 2.227-1.932l.857-6a2.25 2.25 0 0 0-1.883-2.542m-16.5 0V6A2.25 2.25 0 0 1 6 3.75h3.879a1.5 1.5 0 0 1 1.06.44l2.122 2.12a1.5 1.5 0 0 0 1.06.44H18A2.25 2.25 0 0 1 20.25 9v.776" />
 </svg>
 </div>
 
               </li>
               
             ))}
           </ul>
          )}

<p className="text-sm text-muted mt-3">{groupedSessions.previously.length > 0 && 'Previously:'}</p>
{groupedSessions.previously.length > 0 && (
  <ul className="fade-ulist no-scroll">
           {groupedSessions.today.map((session) => (
             <li
               className={`fade-list${session.id === selectedSession ? ' selected-session' : ''}`}
               key={session.id}
               onClick={() => {
                 onSessionClick(session.id);
                 if (window.innerWidth <= 768) {
                   toggleDropdown();
                 }
               }}
             >
               {session.id}
               <div className="svg-chathistory-container">
<svg
xmlns="http://www.w3.org/2000/svg"
width="24"
height="24"
viewBox="0 0 24 24"
fill="none"
stroke="white"
strokeWidth="2"
strokeLinecap="round"
strokeLinejoin="round"
onClick={() => {
setIsShareChatOpen(true);
}}
>
<circle cx="12" cy="12" r="1"></circle>
<circle cx="19" cy="12" r="1"></circle>
<circle cx="5" cy="12" r="1"></circle>
</svg>

<svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="white" width="18" height="18">
<path strokeLinecap="round" strokeLinejoin="round" d="M3.75 9.776c.112-.017.227-.026.344-.026h15.812c.117 0 .232.009.344.026m-16.5 0a2.25 2.25 0 0 0-1.883 2.542l.857 6a2.25 2.25 0 0 0 2.227 1.932H19.05a2.25 2.25 0 0 0 2.227-1.932l.857-6a2.25 2.25 0 0 0-1.883-2.542m-16.5 0V6A2.25 2.25 0 0 1 6 3.75h3.879a1.5 1.5 0 0 1 1.06.44l2.122 2.12a1.5 1.5 0 0 0 1.06.44H18A2.25 2.25 0 0 1 20.25 9v.776" />
</svg>
</div>

             </li>
             
           ))}
         </ul>
          )}
        </div>
   
{isShareChatOpen && (
  <ShareChat selectedSession={selectedSession} closeShareChat={closeShareChat} />
)}  

</>
      )}
    </div>
  );
};


export default ChatHistory;